export default class ScorePanel{
    score:number=0
    level:number=1
    scoreEl:HTMLElement = document.getElementById("scoreId")!
    levelEl:HTMLElement = document.getElementById("levelId")!
    // maxLevel 最大等级
    maxLevel: number
    // 多少分升一级
    upScore:number
    constructor(maxLevel=10,upScore=2){
        this.maxLevel=maxLevel
        this.upScore=upScore
    }
    addScore(){
        this.scoreEl.innerHTML = `${++this.score}`
        if(this.score % this.upScore === 0){
            this.upLevel()
        }
    }
    upLevel(){
        if(this.level < this.maxLevel){
            this.levelEl.innerHTML = `${++this.level}`
        }
    }
}