                <span>SCORES: <span id="scoreId">0</span></span>
            </div>
            <div>
                <span>LEVEL: <span id="levelId">1</span></span>
            </div>
        </div>
    </div>
</template>
<script>
import { onMounted, ref } from "vue";
import GameControl from "./model";
export default {
    name: "SnakeGame",
    setup() {
        const foodRef = ref(null);
        const snakeRef = ref(null);
        onMounted(() => {
            new GameControl(snakeRef.value, foodRef.value);
        });
        return {
            foodRef,
            snakeRef,
        };
    },
