export default class Snake {
    // 表示蛇的元素
    snakeRef: HTMLElement
    // 蛇头
    snakeHead: HTMLElement
    // 蛇身(包括头)
    snakeBody: HTMLCollection

    constructor(snakeRef: HTMLElement) {
        this.snakeRef = snakeRef
        this.snakeHead = snakeRef.children[0] as HTMLElement
        this.snakeBody = snakeRef.children
    }

    // 蛇头 x坐标
    get X() {
        return this.snakeHead.offsetLeft
    }
    // 蛇头 y坐标
    get Y() {
        return this.snakeHead.offsetTop
    }

    set X(value: number) {
        if (this.X === value) return
        if (value < 0 || value > 290)
            throw new Error("撞墙了哟");
        // 水平方向移动时,如果向左移动，就不能向右掉头,反之亦然
        if (this.snakeBody.length > 1 && (this.snakeBody[1] as HTMLElement).offsetLeft === value) {
            if (value > this.X) {
                // 说明继续向右走(就要减去)
                value = this.X - 10
            } else {
                value = this.X + 10
            }
        }
        this.moveBody()
        this.snakeHead.style.left = `${value}px`;
        this.checkHeadBody()
    }
    set Y(value: number) {
        if (this.Y === value) return
        if (value < 0 || value > 290)
            throw new Error("撞墙了哟");
        if (this.snakeBody.length > 1 && (this.snakeBody[1] as HTMLElement).offsetTop === value) {
            if (value > this.Y) {
                // 说明继续向下走(就要减去)
                value = this.Y - 10
            } else {
                value = this.Y + 10
            }
        }
        this.moveBody()
        this.snakeHead.style.top = `${value}px`;
        this.checkHeadBody()
    }
    // 长身体
    addBody() {
        this.snakeRef.insertAdjacentElement("beforeend", document.createElement("div"))
    }
    // 移动身体
    moveBody() {
        for (let i = this.snakeBody.length - 1; i >= 1; i--) {
            let X = (this.snakeBody[i - 1] as HTMLElement).offsetLeft;
            let Y = (this.snakeBody[i - 1] as HTMLElement).offsetTop;
            (this.snakeBody[i] as HTMLElement).style.left = `${X}px`;
            (this.snakeBody[i] as HTMLElement).style.top = `${Y}px`;
        }
    }
    // 检测是否撞到自己
    checkHeadBody() {
        for (let i = 1; i < this.snakeBody.length; i++) {
            let bd = this.snakeBody[i] as HTMLElement
            if (this.X === bd.offsetLeft && this.Y === bd.offsetTop) {
                throw new Error("撞到自己了");
            }
        }
    }
}