import Food from "./food";
import ScorePanel from "./scorePanel";
import Snake from "./snake";

export default class GameControl {
    snake: Snake
    food: Food
    score: ScorePanel
    // 移动方向
    direction: string
    // 是否存活
    isLive: boolean
    constructor(snakeRef: HTMLElement, foodRef: HTMLElement) {
        this.snake = new Snake(snakeRef)
        this.food = new Food(foodRef)
        this.score = new ScorePanel()
        this.direction = "ArrowRight"
        this.isLive = true
        this.init()
    }
    init() {
        document.addEventListener("keydown", this.keyDownHandler.bind(this))
        this.snakeRun()
    }
    /**
     * 键盘响应函数
     * ArrowUp
     * ArrowRight
     * ArrowDown
     * ArrowLeft
     */
    keyDownHandler(e: KeyboardEvent) {
        this.direction = e.key
        console.log(e.key)
    }
    /**
     * 控制蛇的移动
     */
    snakeRun() {
        let X = this.snake.X
        let Y = this.snake.Y

        switch (this.direction) {
            case "ArrowUp":
                Y -= 10
                break
            case "ArrowDown":
                Y += 10
                break
            case "ArrowLeft":
                X -= 10
                break
            case "ArrowRight":
                X += 10
                break
            default:
                break
        }

        this.checkEat(X,Y)

        try {
            this.snake.X = X
            this.snake.Y = Y
        } catch (e:any) {
            console.log(e)
            alert(e.message)
            this.isLive = false
        }

        this.isLive && setTimeout(this.snakeRun.bind(this), 300 - (this.score.level - 1) * 30)
    }
    checkEat(X:number,Y:number){
        if(X === this.food.X && Y === this.food.Y){
            console.log("蛇吃到食物了")
            this.score.addScore()
            this.food.change()
            this.snake.addBody()
        }
    }
    restart(){
        
    }
}